.item-paged {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    &__characteristics-definition {
        margin-inline-start: 0px;
        padding: 5px 0px;
    }

    &__characteristics-list {
        margin: 10px 0;
        color: #111;
        font-family: 'Merriweather';
        font-size: 18px;
        line-height: 27px; 
    }

    &__availability-label {
        display: flex;
        align-items: center;
        gap: 10px;
        
        color: #111;
        font-family: 'Merriweather';
        font-size: 18px;
        line-height: 27px; 
    }

    &__img {
        width: 560px;
    }

    &__characteristics-term {
        float: left;
        margin: 0px 5px 0px 0px;
        padding: 5px 0px;
    }

    &__column {
        display: flex;
        flex-direction: column;
        flex-basis: 48%;
    }
    
    &__column:last-child {
        align-items: center;
        justify-content: center;
        gap: 20px;
    }
    
    &__characteristics-term::after {
        content: ': ';
    }
    
    &__name-label {
        color: #111;
        font-family: 'Merriweather';
        
        font-size: 28px;
        line-height: 40px;
    
        margin-top: 10px;
        margin-bottom: 10px; 
    }

    &__price-label {
        color: #111;
        font-family: 'Merriweather';
        font-size: 22px;
        line-height: 33px; 
    }

    &__purchase-row {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 25px;
    }
}
